* {
	margin: 0;
	padding: 0;
}

.container {
	font-family: "Nunito", sans-serif;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	width: 300px;
	transform-style: preserve-3d;
}

.container .box {
	position: relative;
	width: 250px;
	height: 400px;
	background-color: #13422c !important;
	box-shadow: #2ada82 !important;
	border-radius: 20px !important;
	transform-style: preserve-3d !important;
}

.container .box::before {
	content: "Covid 19";
	position: absolute;
	top: 20px;
	right: 30px;
	font-size: 3.5em;
	font-weight: 900;
	color: white;
	font-style: italic;
	opacity: 0;
	transition: 0.5s;
}
.container .box::after {
	content: "Virus";
	position: absolute;
	bottom: 15px;
	right: 50px;
	font-size: 4.5em;
	font-weight: 900;
	color: white;
	font-style: italic;
	opacity: 0;
	transition: 0.5s;
}

.container .box:hover::before,
.container .box:hover::after {
	opacity: 0.04;
}

.container .box .name {
	position: absolute;
	top: 0;
	left: 0;
	text-align: center;
	color: white;
	width: 100%;
	transform-style: preserve-3d;
	transform: translate3d(0, 0, 75px);
	transition: 0.5s;
	opacity: 0;
	z-index: 10;
}

.container .box img {
	animation: bounce 3s ease infinite;
}

.container .box:hover img {
	animation: none;
}

.container .box:hover .name {
	top: 40px;
	opacity: 1;
}

.container .box .buy {
	position: absolute;
	bottom: 5px;
	left: 50%;
	transform-style: preserve-3d;
	transform: translate3d(-50%, 0, 75px);
	color: black;
	background-color: #2ada82;
	padding: 10px 25px;
	border-radius: 10px;
	text-decoration: none;
	transition: 0.5s;
	opacity: 0;
	z-index: 10;
}

.container .box:hover .buy {
	bottom: 30px;
	opacity: 1;
}

.container .buy:hover {
	background-color: #538513;
	color: rgb(188, 255, 233);
}

.container .box .square {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 200px;
	height: 200px;
	border-radius: 15%;
	transition: 0.5s;
	background: radial-gradient(#9198e5, #13422c);
	z-index: 10;
	opacity: 1;
	transform-style: preserve-3d;
	transform: translate3d(-50%, -50%, 50px);
}

.container .box .product {
	position: absolute;
	top: 50%;
	left: 50%;
	max-width: 300px;
	transition: 0.5s;
	z-index: 15;
	transform-style: preserve-3d;
	transform: translate3d(-50%, -50%, 50px);
}

@keyframes bounce {
	from {
		top: 240px;
		animation-timing-function: ease-out;
	}

	25% {
		top: 220px;
		animation-timing-function: ease-in;
	}

	50% {
		top: 240px;
		animation-timing-function: ease-out;
	}

	75% {
		top: 220px;
		animation-timing-function: ease-in;
	}

	to {
		top: 240px;
	}
}
